.show {
  width: 25%;
  position: relative;
  height: 95vh;
}
.show::after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.8));
  width: 100%;
  height: 60%;
  z-index: 10;
}
.content {
  width: 60%;
  position: absolute;
  bottom: 0;
  z-index: 30;
  margin-left: 50px;
}
.show h4 {
  font-size: 30px;
  color: #fff;
}
.show h4::before {
  content: "";
  display: block;
  width: 100px;
  height: 2px;
  background-color: #fff;
  margin-bottom: 40px;
}
.show span {
  font-size: 15px;
  line-height: 30px;
  margin-bottom: 10px;
  display: block;
  color: #fff;
  overflow: auto;
  overflow-y: hidden;
  height: 0;
  transition: 0.5s;
}
.show:hover span {
  height: 200px;
}
.slider {
  width: 100px;
  height: 10px;
  margin-bottom: 60px;
}
.slider div {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin-left: 5px;
  border: 1px solid #fff;
  border-radius: 50%;
}
.slider .selected {
  background-color: #fff;
}
