.dot {
  position: absolute;
}
img {
  position: absolute;
}
.content1,
.content2,
.content3,
.content4,
.content5,
.content6,
.content7 {
  position: relative;
  width: 100%;
}
.content1 img {
  left: 10vw;
  top: 10vh;
}
.content2 img {
  right: 15vw;
  top: 20vw;
}
.content3 img {
  left: 15vw;
  top: 55vw;
  z-index: 1;
}
.content4 img {
  right: 20vw;
  top: 145vh;
  z-index: 0;
}
.content5 img {
  left: 15vw;
  top: 190vh;
}
.content6 img {
  left: 35vw;
  top: 230vh;
}
.content7 img {
  right: 20vw;
  top: 240vh;
}

