:root {
  --white: #fff;
}
.Logo {
  font-size: 40px;
  margin-left: 5vw;
  color: var(--white);
}
.LogoNav {
  font-size: 40px;
  color: var(--white);
  text-align: center;
  margin-top: 30px;
}
.Navbar {
  width: 100%;
  height: 85px;
  position: absolute;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 1;
}
.Navbar ul {
  display: inline-block;
}
.Navbar ul li {
  list-style: none;
  display: inline-block;
  margin: 0 10px;
}
.Navbar ul li i {
  font-size: 30px;
  color: var(--white);
}
.slideMenu {
  width: 400px;
  height: 100%;
  position: fixed;
  top: 0;
  right: -400px;
  transition: 0.5s;
  background-color: #232323;
  z-index: 40;
}
.slideMenu ul {
  list-style: none;
  font-size: 40px;
  height: 80%;
  margin-top: 200px;
}
.slideMenu ul li {
  margin-top: 50px;
  margin-left: 90px;
}
.slideMenu ul li a::before {
  content: "";
  display: block;
  width: 60px;
  height: 2px;
  background-color: var(--white);
  transition: 0.5s;
}

.slideMenu ul li a {
  display: inline-block;
  text-decoration: none;
  color: var(--white);
}

.slideMenu ul li a:hover::before {
  width: 100%;
}
.slideMenu ul .exit::before {
  width: 0;
}
.exit {
  position: absolute;
  top: 30px;
  right: 30px;
}
.exit a {
  color: var(--white);
}
